@import url(https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap);

body {
    text-align: center;
    font-family: 'Lexend Deca', sans-serif;
}
.Items {
    max-width: 80%;
    margin: auto;
    margin-top: 50px;
}

.Items button {
    border-radius: 10px;
    border-color:black;
    border-width:5px;
    margin:2px;
    background-color: rgb(180, 180, 180);
    box-sizing: border-box;
    border-style: solid;
}

.Items button:hover{
    background-color: white; 
}

.Items button:disabled{
    border-color: yellow;
    background-image: linear-gradient(to bottom right,tomato, khaki, lightgreen, lightskyblue,mediumpurple);
}

.Items img {
    width: 80px;
    max-width: 10vw;
}
.Farms img {
    width:40px;
}

.Farms .farmInfo {
    border-radius: 10px;
    padding: 5px;
    margin:auto;
    margin-top: 5px;
    margin-bottom: 5px;
    max-width: 80%;

}

.Farms .farmInfo:nth-child(2n) {
    background-color: rgb(180, 180, 180);
}
